import React, { useState, useEffect } from 'react';
import LogOut from '../LogOut';
import Card from 'react-bootstrap/Card';
import Modal from 'react-bootstrap/Modal';
import Image from 'react-bootstrap/Image';
import { Button, Form, Col } from 'react-bootstrap';
// import AddVaction from './AddVaction';
import { domain } from '../config';


export default function VacationAdmin() {

    const [description, setDescription] = useState('');
    const [location, setLocation] = useState('');
    const [image, setImage] = useState('');
    const [start, setStart] = useState('');
    const [end, setEnd] = useState('');
    const [price, setPrice] = useState('');
    const [vacList, setVacList] = useState([]);
    const [deleteVac, setDeleteVac] = useState(0);
    const [editVac, seteditVaction] = useState(0);
    const [vacId, setVacId] = useState(0);
    const [show, setShow] = useState(false);
    const [whichButton, setWhichButton] = useState('');
    const handleClose = () => setShow(false);
    function handleShow(whichBtn) {
        setShow(true);

        if (whichBtn === 'edit') {
            setDescription(vacId.description);
            setLocation(vacId.location);
            setPrice(vacId.price);
            setImage(vacId.image);
            setStart(vacId.start_at);
            setEnd(vacId.end_at)
        }
    }
    useEffect(() => {
        async function vacationAdmin() {
            const req = await fetch(domain + `/vactionAdmin`);
            const data = await req.json();
            console.log(data);
            setVacList(data);
        }
        vacationAdmin();
    }, [deleteVac, editVac])


    async function dleteVacation(vacId) {
        const req = await fetch('http://localhost:3001/deleteVacation', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ vacId })
        });
        const data = await req.text();
        setDeleteVac(new Date())
        console.log(data);
    }


    async function editVaction() {
        alert(vacId.vac_id)
        const req = await fetch(domain + `/${whichButton}`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                description: description,
                location: location,
                image: image,
                start_at: start,
                end_at: end,
                price: price,
                vacId
            })
        });

        const data = await req.json();
        console.log(data);
        setShow(false)
        seteditVaction(new Date())
    }



    /////////////////
    return (
        <div >
            <button onClick={() => { handleShow(); setWhichButton('addVacation') }}>add vacation</button>
            <LogOut />
            { vacList.map(item => {
                return (
                    <Card className='Card' style={{ width: 'auto', maxWidth: '200px', height: 'auto', backgroundColor: 'white', marginLeft: '2px', borderColor: 'lightblue', display: 'inline-block' }}>
                        <button style={{ color: 'red', float: 'right', marginRight: '5px' }} onClick={() => dleteVacation(item.vac_id)}>x</button><br /><br />
                        <button onClick={() => { handleShow('edit'); setVacId(item); setWhichButton('editVacation') }} style={{ color: 'gray', float: 'right', marginRight: '5px' }}>{<img src='https://cdn.onlinewebfonts.com/svg/img_530071.png' style={{ height: '20px' }} />}</button>
                        <p>description: {item.description}</p>
                        <p>location: {item.location}</p>
                        <p style={{ textAlign: 'left' }}>${item.price}</p>
                        <Image key={item.image} src={item.image} fluid style={{ maxWidth: '200px', maxHeight: 'auto' }} />
                        <p>start: {item.start_at} end: {item.end_at} </p>
                        <p style={{ textAlign: 'left' }}>{item.vac_id}</p>
                    </Card>
                )
            })
            }


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                </Modal.Header>
                <Modal.Body>
                    <Form style={{ marginLeft: '2%', marginRight: '2%' }} >
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom01">
                                <Form.Label>Description</Form.Label>
                                <Form.Control
                                    // onBeforeInputCapture={setDescription(vacId.description)}

                                    onChange={(e) => { setDescription(e.target.value) }}
                                    required
                                    type="text"
                                    placeholder="Description"
                                    defaultValue={vacId.description}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>Price</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setPrice(vacId.price)}
                                    onChange={(e) => setPrice(e.target.value)}
                                    type="number"
                                    placeholder="Price"
                                    required
                                    defaultValue={vacId.price}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom02">
                                <Form.Label>Location</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setLocation(vacId.location)}
                                    onChange={(e) => setLocation(e.target.value)}
                                    required
                                    type="text"
                                    placeholder="Location"
                                    defaultValue={vacId.location}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustomUsername">
                                <Form.Label>Image</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setImage(vacId.image)}
                                    onChange={(e) => setImage(e.target.value)}
                                    type="text"
                                    placeholder="Image"
                                    aria-describedby="inputGroupPrepend"
                                    required
                                    defaultValue={vacId.image}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>Start</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setStart(vacId.start_at)}
                                    onChange={(e) => setStart(e.target.value)}
                                    type="date"
                                    placeholder="Start"
                                    required
                                    defaultValue={vacId.start_at}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>End</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setEnd(vacId.end_at)}
                                    onChange={(e) => setEnd(e.target.value)}
                                    type="date"
                                    placeholder="End"
                                    required
                                    defaultValue={vacId.end_at}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Button type='button' onClick={() => editVaction(whichButton)}>send</Button>
                    </Form>
                </Modal.Body>
            </Modal>
        </div >
    )
}