import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { domain } from '../config';

import {
  VictoryBar, VictoryChart, VictoryAxis,
  VictoryTheme, VictoryStack
} from 'victory';


// const data = [
//   { quarter: 1, earnings: 13 },
//   { quarter: 2, earnings: 16 },
//   { quarter: 3, earnings: 14 },
//   { quarter: 4, earnings: 19 }
// ];

export default function Statistics() {
  const [data, setFollowedVac] = useState([]);
  const [dataLen, setLen] = useState([]);
  const [dataName, setName] = useState([]);
  useEffect(() => {
    async function fn() {
      const req = await fetch(domain + '/vacationFollowed');
      const dataReq = await req.json();
      setLen(dataReq[0].map(item => {
        return ({ quarter: item.vac_id, earnings: item.followers_mount })
      }))
      setName(dataReq[0].map(item => {
        return ({ quarter: item.vac_id, earnings: item.followers_mount })
      }))
    }
    fn();
  }, [])
  console.log(dataLen)
  return (
    <div style={{ width: '50%', height: '40%' }}>
      <VictoryChart
        // domainPadding will add space to each side of VictoryBar to
        // prevent it from overlapping the axis
        theme={VictoryTheme.material}
        domainPadding={{ x: 30 }}
      >
        <VictoryAxis style={{ width: '40%', height: '40%' }}
          // tickValues specifies both the number of ticks and where
          // they are placed on the axis
          tickValues={[dataLen.length]}
          tickFormat={[dataLen.map(item => { return (item.quarter) })]}
        />
        <VictoryAxis
          dependentAxis
          // tickFormat specifies how ticks should be displayed
          tickFormat={(x) => x}
        />
        <VictoryBar
          style={{ data: { fill: 'rgb(10 10 253 / 53%)' } }}
          barWidth={({ index }) => index * 2 + 8}
          data={dataLen}
          alignment="start"
          x="quarter"
          y="earnings"
        />
      </VictoryChart>
    </div>
  )
}