import React, { useState } from 'react';
import { useHistory } from "react-router-dom";
import { Form, Col } from 'react-bootstrap';
// import { Route } from 'react-router';
import { domain } from './config';

export default function LogIn() {
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const history = useHistory();

    async function signInHandler() {
        if (userName === "" || password === "") {
            return alert('user name or password empty')
        }
        try {
            const sendReq = await fetch(domain + `/login`, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    user_name: userName,
                    password: password
                })
            });
            const data = await sendReq.json()

            if (data.user_id !== '') {
                localStorage.setItem('userInfo', JSON.stringify({ userName, password, userId: data[0].user_id, isLoggedIn: true, isAdmin: data[0].is_admin }));
                if (data[0].is_admin) {
                    return history.push("./VacationAdmin");
                }
                history.push("./VacationList");
            } else if (data.error === 'user name or password dosnet match') {
                alert('user name or password dosnet match');
            }
            console.log(data);
        }
        catch (err) {
            console.log('error fetching', err);
        }
    }
    function signUpHandler() {
        debugger
        history.push("./SignUp");
        console.log(history.push("./SignUp"))
    }

    return (
        <div className='divv' style={{ }}>
            <Form style={{ marginLeft: '35%' }}>
                <Form.Row>
                    <Form.Group as={Col} md="4" controlId="validationCustomUsername">
                        <Form.Label>Username</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Username"
                            required
                            onChange={(e) => setUserName(e.target.value)}
                        />
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} md="4" controlId="validationCustom03">
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                            type="password"
                            placeholder="Password"
                            required
                            onChange={(e) => setPassword(e.target.value)} />
                    </Form.Group>
                </Form.Row>
                <button type='button'  onClick={signInHandler}>LogIn</button>
                <button type='button' onClick={signUpHandler} style={{ marginLeft: '20px' }}>Register</button>
            </Form>
        </div >
    )
}