import React, { useState, useEffect } from 'react';
import LogOut from '../LogOut';
import Card from 'react-bootstrap/Card';
import Image from 'react-bootstrap/Image';
import SerachVacation from './SerachVacation'
import VacationList from './VacationList.css';
import { domain } from '../config';

export default function VactionList() {
    let user = JSON.parse(localStorage.getItem('userInfo'));

    console.log('the of x is: ', user)
    const [vacList, setVacList] = useState([]);
    const [isfollowe, setIsfollwe] = useState('');
    // const [search, setSearch] = useState('');
    // const [date, setDate] = useState('');

    useEffect(() => {
        async function vacations() {
            const req = await fetch(domain + `/vaction/${user.userId}`);
            const data = await req.json();
            setVacList(data[0]);
            console.log(vacList);
        }
        vacations();
    }, [isfollowe])


    async function followHandler(vacation) {
        try {
            await fetch(domain + `/isFollow`, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ user_id: user.userId, vacation })
            });
            setIsfollwe(new Date());
            console.log('the followe is ', isfollowe);
        }
        catch (err) {
            console.log(err);
        }
    }

    return (
        <div>
            <SerachVacation setVacList={setVacList} />
            <LogOut />
            <br />
            <p style={{ float: 'right' }}> hello {user.userName}</p>
            { vacList.map(item => {
                return (
                    <div style={{ float: 'left' }}>
                        <Card className='Card' style={{ width: '17rem', backgroundColor: 'white', marginLeft: '2px', borderColor: 'lightblue' }}>
                            <input type="checkbox" checked={item.follower_id != null} id="checkbox" onChange={() => followHandler(item.vac_id)} style={{ float: 'right' }} />
                            <p>location: {item.location}</p>
                            <p>description: {item.description}</p>
                            <p style={{ textAlign: 'left' }}>${item.price}</p>
                            <Image key={item.image} src={item.image} fluid style={{ maxWidth: '100px', maxHeight: 'auto' }} />
                            <p>start: {item.start_at} end: {item.end_at} </p>
                            <p style={{ textAlign: 'left' }}>{item.followers_mount} f</p>
                            <p style={{ textAlign: 'left' }}>{item.vac_id}</p>
                        </Card>
                    </div>
                )
            })
            }
        </div >
    )
}