import React, { useState } from 'react'
import Card from 'react-bootstrap/Button';
import Image from 'react-bootstrap/Image';
import { domain } from '../config';

export default function SerachVacation({ setVacList }) {
    const [search, setSearch] = useState('');
    const [date, setDate] = useState('');
    const [filterVacation, setFilterVacation] = useState([]);
    let user = JSON.parse(localStorage.getItem('userInfo'));

    async function serachHandel() {
        try {
            const sendReq = await fetch(domain + '/searchVacation', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ search, date, user: user.userId })
            });
            const data = await sendReq.json();
            setVacList([]);
            setFilterVacation(data[0]);
            console.log(data)
        }
        catch (err) {
            console.log(err);
        }
    }


    async function followHandler(vacation) {
        try {
            await fetch(`http://localhost:3001/isFollow`, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ user_id: user.userId, vacation })
            });
            serachHandel();

        }
        catch (err) {
            console.log(err);
        }
    }
    async function clearHandel() {
        try {
            const req = await fetch(`http://localhost:3001/vaction/${user.userId}`);
            const data = await req.json();
            setFilterVacation(data[0]);
        }
        catch (err) {
            console.log(err);
        }
    }

    return (
        <div>
            <input type='text' onChange={(e) => { setSearch(e.target.value) }} />
            <input type="date" onChange={(e) => { setDate(e.target.value) }} />
            <button onClick={serachHandel}>Search</button>
            <button onClick={clearHandel}>clear</button>
            <br /><br />

            {filterVacation.map(item => {
                return (
                    <div style={{ float: 'left' }}>
                        <Card className='Card' style={{ width: '17rem', backgroundColor: 'white', marginLeft: '2px', borderColor: 'lightblue' }}>
                            <input type="checkbox" checked={item.follower_id != null} id="checkbox" onChange={() => followHandler(item.vac_id)} style={{ float: 'right' }} />
                            <p>location: {item.location}</p>
                            <p>description: {item.description}</p>
                            <p style={{ textAlign: 'left' }}>${item.price}</p>
                            <Image key={item.image} src={item.image} fluid />
                            <p>start: {item.start_at} end: {item.end_at} </p>
                            <p style={{ textAlign: 'left' }}>{item.followers_mount} f</p>
                            <p style={{ textAlign: 'left' }}>{item.vac_id}</p>
                        </Card>
                    </div>
                )
            })}
        </div>
    )
}